import React from 'react';
import Slider from 'react-animated-slider';
import 'react-animated-slider/build/horizontal.css';
import 'normalize.css/normalize.css';
import './slider-animations.css';
import './styles.css';
import { Helmet } from 'react-helmet';

const content = [
	{
		title: 'Ningen no 99% wa Hon de Dekite Iru',
		description:
		'Ayukawa adalah seorang mahasiswa yang beranggapan bahwa buku segalanya baginya, buku adalah kecerdasan dari umat manusia itu sendiri, sehingga sifat manusia yang berkebalikan dengan buku, membuatnya membenci manusia... tapi, meski dia menyatakan bahwa dirinya membenci manusia, ada seseorang yang dia sukai, itu adalah seorang pustakawan yang dia temu di perpustakaan, karena rasa sukanya itu, dia akhirnya sering pergi ke perpustakaan. Tapi, meski dia sering ke perpustakaan, hubungannya tidak ada kemajuan sama sekali, sehingga dia penasaran apakah ada buku yang dapat membantunya.., dan diapun menemukan sebuah buku tentang ritual.. singkat cerita, dia melakukan ritual yang ada di buku itu, dikatakan bahwa apabila kamu mengharapkan seseorang yang kamu impikan saat melakukan ritual tersebut, orang itu akan muncul di depanmu, tapi, bukannya mengharapkan pustakawan tersebut, dia justru mengharapkan seseorang yang dapat memberinya saran percintaan. Seseorang itu muncul, tapi dia bukan manusia melainkan tsukumogami yang berasal dari buku-buka di rak bukunya... ',
		button: 'Baca',
		image: 'slider-content Tsukumogami',
		genre: 'Romance, Web Comic, Comedy, Slice of Life',
    href: '#/Ningen-no-99-Pasento-wa-Hon-de-Dekite-Iru'
	},
	{
		title: 'Hina to Bambi',
		description:
		'Di musim semi sepasang teman masa kecil, taku dan hinata akan masuk ke sma di kota, keseharian bersama sepanjang waktu di sebuah desa pegunungan akan berubah...?! ',
		button: 'Baca',
		image: 'slider-content hinato',
		genre: 'Romance, Serial, Comedy, School Life, Lice of Life',
    href: '#/Hina-to-Bambi'
	},
	{
		title: 'Tagahumika',
		description:
		'Wakui menjalani kehidupan kampus yang membosankan. Satu-satunya kesenangannya adalah korespondensi. Berawal dari sebuah surat yang ia temukan di perpustakaan universitas dan menjadi pusat kehidupan Wakui. Namun, suatu hari, Wakui menemukan sebuah surat yang berisi sesuatu yang tidak seharusnya diketahui oleh korespondennya. Wakui mencurigai bahwa koresponden mungkin adalah kenalannya, dan mulai mencurigai tiga gadis yang terkait dengannya. Namun, ketiga gadis itu agak eksentrik..',
		button: 'Baca',
		image: 'slider-content Tagahumika',
		genre: 'Romance, Drama, Web Comic, Phsychological, Slice of Life',
    href: '#/Tagahumika'
	},
  {
    title: 'Asuka',
		description:
		'Takamichi Mishima adalah seorang siswa SMA yang membosankan di klub studi film. Suatu hari, kehidupan sehari-harinya berubah total. Dia sedang merencanakan film tentang Asuka, heroine idamannya, saat Asuka muncul di depannya. Mishima, yang percaya bahwa [perasaannya sendiri yang melahirkan Asuka di dunia ini], memulai hari-harinya yang seperti mimpi. ......',
		button: 'Baca',
		image: 'slider-content Asuka',
		genre: 'Romance, Drama, Oneshot, School Life, Slice of Life',
    href: '#/Asuka'
  }
];

export const Carousel = () => (
	<div>
    <Helmet>
        <style>{`
            .btn-carousel {
                -webkit-appearance: none;
                        appearance: none;
                -webkit-filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
                        filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
                -webkit-transition: all .5s ease;
                       filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
                -webkit-transition: all .5s ease;
                        transition: all .5s ease;
                border: none;
                background: rgb(225, 225, 225, 0.8);
                border-radius: 30px;
                text-transform: uppercase;
                box-sizing: border-box;
                padding: 15px 40px;
                font-weight: 400;
                font-size: 13px;
                cursor: pointer;
              }
              
              .btn-carousel:hover {
                color: #FFFFFF;
                background: #222222;
                -webkit-filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
                        filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
              }
              
              .wrapper {
                max-width: 1335px;
                box-sizing: border-box;
                padding: 0 20px;
                margin: auto;
              }
              
              .wrapper a {
                display: inline-block;
                margin: 5px;
              }
              
              .wrapper a:first-child {
                margin-left: 0;
              }
              
              .slider-wrapper {
                position: relative;
                height: 70vh;
                overflow: hidden;
                border-radius: 0 0 20px 20px;
                box-shadow: 0px 10px 14px 0px rgba(0,0,0,0.37);
                -webkit-box-shadow: 0px 10px 14px 0px rgba(0,0,0,0.37);
                -moz-box-shadow: 0px 10px 14px 0px rgba(0,0,0,0.37);
              }
              
              .slide {
                height: 70vh;
                background-size: cover !important;
              }
              
              .slide::before {
                  content: '';
                  display: block;
                  position: absolute;
                  width: 100%;
                  height: 100%;
                  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, 0.9)));
                  background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
                  bottom: 0;
                  left: 0;
              }
              
              .previousButton, .nextButton {
                      position: absolute;
                      top: 50%;
                      transform: translateY(-50%);
                      z-index: 3;
                      background: url('https://uxwing.com/wp-content/themes/uxwing/download/arrow-direction/next-button-icon.png') no-repeat center center / 16px;
                      width: 32px;
                      height: 32px;
                      text-indent: -9999px;
                      cursor: pointer;
                      filter: invert(80%);
              }
              
              .previousButton {
                left: 0;
                -webkit-transform: rotate(180deg) translateY(calc(50% + 0px));
                        transform: rotate(180deg) translateY(calc(50% + 0px));
              }
              
              .nextButton {
                right: 0;
              }
              
              .slider-content {
                text-align: center;
                background-position: center;
              }
              
              .slider-content .inner {
                padding: 0 70px;
                box-sizing: border-box;
                position: absolute;
                width: 100%;
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%, -50%);
                        transform: translate(-50%, -50%);
              }
              
              .slider-content .inner .btn-carousel{
                -webkit-transition-delay: 0s;
                        transition-delay: 0s;
              }
              
              .slider-content .inner h1 {
                font-weight: 600;
                margin: 0 auto;
                max-width: 840px;
                color: #FFFFFF;
                
                line-height: 1;
                font-family: 'poppins';
              }
              
              .slider-content .inner p {
                color: #FFFFFF;
                font-size: 12px;
                line-height: 1.5;
                margin: 20px auto 30px;
                --max-lines: 3;
                display: -webkit-box;
                overflow: hidden;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: var(--max-lines);
                font-family: 'poppins', sans-serif;
                max-width: 640px;
              }
              
              .slider-content section {
                position: absolute;
                bottom: 20px;
                left: 20px;
              }
              
              .slider-content section span {
                color: #FFFFFF;
              }
              
              .slider-content section span {
                color: rgba(255, 255, 255, 0.5);
                font-size: 12px;
                display: inline-block;
                text-align: left;
                line-height: 1.4;
                vertical-align: middle;
                margin-left: 10px;
                font-family: 'Poppins';
              }
              
              .slider-content section img {
                width: 40px;
                height: 40px;
                border: solid 2px rgba(255, 255, 255, 0.5);
                border-radius: 100%;
                vertical-align: middle;
              }
              
              .slider-content section span strong {
                color: #FFFFFF;
                font-size: 14px;
                display: block;
              }
              
        `}</style>
    </Helmet>
		<Slider className="slider-wrapper">
			{content.map((item, index) => (
				<div
					key={index}
					className={item.image}
				>
					<div className="inner">
						<h1>{item.title}</h1>
						<p>{item.description}</p>
						<a href={item.href}><button className='btn-carousel'>{item.button}</button></a>
					</div>
					<section>
						<span>
							Genre:<br/><span>{item.genre}</span>
						</span>
					</section>
				</div>
			))}
		</Slider>
	</div>
);